@import (less) "color.less";

:host {
    position:relative;
    display: block;
}


input.ng-invalid.ng-touched.show-validation,
input.ng-invalid.forced-touched.show-validation,
:host(.ng-invalid) input.show-validation.ng-touched,
:host(.ng-invalid) input.show-validation.forced-touched
{
    border: 2px solid @dark_red;
}


.textfield-box {
    margin: 0 0 0.625em 0;
    padding: 0.625em;//0.375em 1em 0.375em 1em;
    color: @darkest_gray;
    border: 1px solid @darkest_gray;
    border-radius: 2px;
    font-size: 1em;
    height: 3.125em;
    //box-shadow: none !important;
    // box-shadow: inset 0px 0px 4px 0px #323232 !important;
      &:focus {
        border: 2px solid @light_green;
        outline: 0;
        box-shadow: none !important;
    }
    &[disabled] {
        opacity: 0.5;
        cursor: default !important;
        // background-color: @white;
    }
}
::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    color: @gray;
    opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
    color: @gray;
}

::-ms-input-placeholder { /* Microsoft Edge */
    color: @gray;
}

:host(.show-validation.ng-dirty.ng-invalid, .show-validation.nbp-show-validation.ng-invalid) {
    .textfield-box {
        border: 2px solid @dark_red !important;
        -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
        &:focus {
            border: 2px solid @dark_red;
        }
    }
}

.highlight-default {
    border:2px solid @yellow;
}

.round-borders {
    border-radius:25px;
}

.icon-inside{
  position:absolute;
  top:50%;
  right:25px;
  font-size: 26px;
  transform: translateY(-50%);
  &:before{
    color:  @green;
  }
}

:host-context(.contrasto-on) {
    .input-label {
        color: @black;
    }
    .highlight-default {
        border:2px solid @black;
    }
    .input-box {
        color: @black;
        border: 1px solid @black;
        &:focus {
            border: 2px solid @black;
        }
        &::-webkit-input-placeholder {
            color:@black;
        }
        &[disabled] {
            color: @white;
            background-color: @black;
            opacity: 1;
        }
    }
    .has-error {
        .input-box {
            border: 2px solid @black;
            -webkit-box-shadow: inset 0 0px 0px @black;
            box-shadow: inset 0 0px 0px @black;
            &:focus {
                border: 2px solid @black;
            }
        }
    }
    .yellow-border {
        border:2px solid @black;
    }
}
